.player {
    padding-bottom: 10px;
}
.poker-hand {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px; /* Spacing between cards */
}

.card {
    width: 60px;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    font-size: 18px;
    color: black;
    position: relative;
}

/* Individual card colors for different suits */
.s { color: black; }  /* Spades */
.c { color: black; }  /* Clubs */
.d { color: darkred; }    /* Diamonds */
.h { color: darkred; }    /* Hearts */
